.header div {
    flex-direction: row-reverse;
    justify-content: space-between;
    flex-wrap: wrap;
    
}
.header {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
    // background: linear-gradient(rgba(8, 173, 118, 0.555)), url("/img/fck.jpg");
    background: url("/img/fck.jpg");
    background-size: cover;
    background-position: center;
    
    h1 {
        color: aqua;
        padding-top: 2rem;
        font-size: 30px;
        
    }
    span{
        color: darkorange;
    }
    h6 {
        font-size: 6px;
    }   
}
.header h1 {
    background-color: transparent;
    max-width: 100vw;
    padding-top: 1rem;
    padding-left: 2rem;
    // margin: 0;
    padding-bottom: 2rem; 
}
.bar {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    // width: auto;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    // background-color: darkorange;
    
    p{
        display: flex;
        height: auto;
        width: 100%;
        justify-content: center;
        align-items: center;
        letter-spacing: 1rem;
        background-color: transparent;
        color: aqua;
        text-align: center;
        // margin-bottom:1rem;
        font-size: 2rem;
    }

}
/* menu bar!!! */

.header__flex {
    box-shadow: 0 4px 8px 0 rgba(173, 130, 130, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
    background-color: aqua;
    display: flex;
    justify-content: center;
    // flex-direction: column;
    align-items: center;
    max-width: auto;
    padding: 2rem;
    margin: 0;
    // margin: 0 auto;
    
    a {
        
        letter-spacing: 0.3em;
        padding: 0.7rem;
        text-decoration: none;
        margin-bottom: 1rem;
        color: snow;
        font-size: 1rem;
        &:hover {
            text-decoration: underline;

        }
        &:hover {
            color: magenta;
        }
    }
    nav{
        text-align: center;
        
    }
      
    nav a:nth-child(2) {
        color: magenta;
    }   
}

.header-contacts {
    
    letter-spacing: 2rem;
    padding-top: 2rem;
    display: flex;
    div {
        display: flex;
        flex-direction: row;
        position: fixed;
    }
    a { 
        
        color: magenta;
    }
    a:hover {
        color: aqua;
    }
}